'use client'

import { useState } from 'react'
import { Lock } from 'lucide-react'
import Navbar from '@/components/Navbar'

export default function AdminLogin() {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [loading, setLoading] = useState(false)

    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault()
        setLoading(true)
        // Placeholder for login logic
        setTimeout(() => {
            alert('Admin login functionality coming soon!')
            setLoading(false)
        }, 1000)
    }

    return (
        <main className="min-h-screen flex flex-col items-center justify-center relative p-6">
            <Navbar />

            <div className="glass-panel w-full max-w-md p-8 rounded-3xl animate-float">
                <div className="text-center mb-8">
                    <div className="w-16 h-16 bg-gradient-to-br from-indigo-500 to-pink-500 rounded-2xl mx-auto flex items-center justify-center mb-4 shadow-lg transform rotate-3">
                        <Lock className="text-white" size={32} />
                    </div>
                    <h1 className="font-serif text-3xl font-bold mb-2">Admin Portal</h1>
                    <p className="text-sm opacity-60">Security Clearance Required</p>
                </div>

                <form onSubmit={handleLogin} className="space-y-5">
                    <div>
                        <label className="block text-xs font-bold uppercase tracking-widest opacity-50 mb-2 ml-1">Identity</label>
                        <input
                            type="text"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            className="w-full bg-white/5 border border-white/10 rounded-xl px-4 py-3 outline-none focus:border-indigo-500/50 focus:ring-1 focus:ring-indigo-500/50 transition-all placeholder:opacity-20"
                            placeholder="Username"
                        />
                    </div>
                    <div>
                        <label className="block text-xs font-bold uppercase tracking-widest opacity-50 mb-2 ml-1">Passkey</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            className="w-full bg-white/5 border border-white/10 rounded-xl px-4 py-3 outline-none focus:border-pink-500/50 focus:ring-1 focus:ring-pink-500/50 transition-all placeholder:opacity-20"
                            placeholder="••••••••"
                        />
                    </div>

                    <button
                        type="submit"
                        disabled={loading}
                        className="w-full py-4 rounded-xl bg-gradient-to-r from-indigo-600 to-pink-600 font-bold text-white shadow-lg shadow-indigo-500/25 hover:shadow-indigo-500/40 hover:-translate-y-1 active:translate-y-0 transition-all disabled:opacity-50 disabled:pointer-events-none"
                    >
                        {loading ? 'Authenticating...' : 'Access Safe'}
                    </button>
                </form>
            </div>
        </main>
    )
}
